'use client';
import React, { useEffect, useState } from 'react';
import { Text } from '@/components/ui/text';
import { Button } from "@/components/ui/button";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter } from "@/components/ui/dialog";
import { Avatar, AvatarImage, AvatarFallback } from "@/components/ui/avatar";
import { Card, CardContent } from "@/components/ui/card";
import { Input } from '@/components/ui/input';
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { useRouter } from 'next/navigation';
import { useAuth } from '@/context/AuthContext';
import { AiOutlineUser } from "react-icons/ai";
import { BsSearch } from "react-icons/bs";
import ProtectedRoute from '@/components/ProtectedRoute'
import api from '@/lib/api';
import '@/app/globals.css';

// 新增: 加入课题组对话框的Props接口
interface JoinGroupDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
}

// 新增: 加入课题组对话框组件
const JoinGroupDialog: React.FC<JoinGroupDialogProps> = ({ open, onOpenChange }) => {
  const [formData, setFormData] = useState({
    groupId: '',
    inviteCode: ''
  });
  const [errors, setErrors] = useState({
    groupId: '',
    inviteCode: ''
  });
  const [submitted, setSubmitted] = useState(false);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
    // 清除错误信息
    if (value.trim()) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }));
    }
  };

  const handleSubmit = () => {
    const newErrors = {
      groupId: formData.groupId.trim() ? '' : '课题组ID不能为空',
      inviteCode: formData.inviteCode.trim() ? '' : '邀请码不能为空'
    };
    setErrors(newErrors);
    setSubmitted(true);
    if (!newErrors.groupId && !newErrors.inviteCode) {
      // 处理提交逻辑
      console.log('表单提交成功', formData);
      // 这里可以添加API调用或其他操作
    }
  };

  const handleReset = () => {
    setFormData({
      groupId: '',
      inviteCode: ''
    });
    setErrors({
      groupId: '',
      inviteCode: ''
    });
    setSubmitted(false);
  };

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-md">
        <DialogHeader>
          <DialogTitle className="text-center text-2xl font-semibold text-gray-900">加入课题组</DialogTitle>
          <p className="mt-2 text-sm text-gray-500">请输入课题组ID和邀请码完成加入</p>
        </DialogHeader>
        <div className="space-y-4">
          <div className="space-y-2">
            <div className="flex items-center">
              <Label htmlFor="groupId" className="text-sm font-medium text-gray-700">
                课题组ID
              </Label>
              <span className="text-red-500 ml-1">*</span>
            </div>
            <Input
              id="groupId"
              name="groupId"
              value={formData.groupId}
              onChange={handleChange}
              placeholder="请输入课题组ID"
              className={`h-10 focus:ring-2 focus:ring-black focus:border-transparent ${
                errors.groupId && submitted ? 'border-red-500 focus:ring-red-500' : 'border-gray-300'
              }`}
            />
            {errors.groupId && submitted && (
              <p className="text-sm text-red-500 mt-1">{errors.groupId}</p>
            )}
          </div>
          <div className="space-y-2">
            <div className="flex items-center">
              <Label htmlFor="inviteCode" className="text-sm font-medium text-gray-700">
                邀请码
              </Label>
              <span className="text-red-500 ml-1">*</span>
            </div>
            <Input
              id="inviteCode"
              name="inviteCode"
              value={formData.inviteCode}
              onChange={handleChange}
              placeholder="请输入邀请码"
              className={`h-10 focus:ring-2 focus:ring-black focus:border-transparent ${
                errors.inviteCode && submitted ? 'border-red-500 focus:ring-red-500' : 'border-gray-300'
              }`}
            />
            {errors.inviteCode && submitted && (
              <p className="text-sm text-red-500 mt-1">{errors.inviteCode}</p>
            )}
          </div>
        </div>
        {submitted && !errors.groupId && !errors.inviteCode && (
          <Alert className="bg-green-50 text-green-800 border border-green-200">
            <AlertDescription>
              提交成功！正在处理您的请求...
            </AlertDescription>
          </Alert>
        )}
        <div className="flex space-x-4 pt-2">
          <Button
            onClick={handleSubmit}
            className="flex-1 bg-black hover:bg-gray-800 text-white !rounded-button whitespace-nowrap cursor-pointer"
          >
            加入
          </Button>
          <Button
            onClick={handleReset}
            variant="outline"
            className="flex-1 border-black text-black hover:bg-gray-100 !rounded-button whitespace-nowrap cursor-pointer"
          >
            返回
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
};

export default function HomePage() {
  const router = useRouter();
  const { isAuthenticated, logout } = useAuth();
  const [userName, setUsername] = useState('');
  const [loading, setLoading] = useState(true);
  const [searchQuery, setSearchQuery] = useState("");

  // 创建课题组状态
  const [isCreateDialogOpen, setIsCreateDialogOpen] = useState(false);
  const [createFormData, setCreateFormData] = useState({
    name: '',
    institution: '',
    description: '',
    research_area: ''
  });
  
  // 新增: 加入课题组状态
  const [isJoinDialogOpen, setIsJoinDialogOpen] = useState(false);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setCreateFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleCreateSubmit = () => {
    console.log('提交的数据:', createFormData);
    setIsCreateDialogOpen(false);
    // 重置表单
    setCreateFormData({
      name: '',
      institution: '',
      description: '',
      research_area: ''
    });
  };

  // 页面保护：未登录直接跳转
  useEffect(() => {
    if (!isAuthenticated) {
      router.replace('/login');
    }
  }, [isAuthenticated, router]);

  // 拉取用户信息
  useEffect(() => {
    const fetchUser = async () => {
      try {
        const response = await api.get('/api/user/me');
        setUsername(response.data.username);
      } catch (error) {
        console.error('获取用户信息失败');
        setUsername('未知用户');
      } finally {
        setLoading(false);
      }
    };
    if (isAuthenticated) {
      fetchUser();
    }
  }, [isAuthenticated]);

  // 页面保护和加载状态
  if (!isAuthenticated || loading) {
    return (
      <div className="flex justify-center items-center h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-gray-900"></div>
      </div>
    );
  }

  return (
    <ProtectedRoute>
      {/* 顶部导航栏 */}
      <header className="flex items-center justify-between px-4 py-3 border-b border-gray-200">
        <div className="flex items-center">
          <div className="w-6 h-6 mr-2 text-black">
            <i className="fas fa-file-alt"></i>
          </div>
          <h1 className="text-lg font-medium text-black">AI+协同文献管理平台</h1>
        </div>
        
        <div className="flex-1 mx-6">
          <div className="relative">
            <Input
              type="text"
              placeholder="搜索文献..."
              className="w-full px-4 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-gray-400 text-sm"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
            />
            <div className="absolute inset-y-0 right-0 flex items-center pr-3 pointer-events-none text-gray-400">
              <BsSearch className="w-4 h-4" />
            </div>
          </div>
        </div>
        
        <div className="flex items-center">
          <Button
            variant="ghost"
            className="mr-2"
            onClick={() => router.push('/main')}
            title="点击返回主界面"
          >
            {userName}
          </Button>
          {isAuthenticated && (
            <Button variant="outline" size="sm" onClick={logout}>
              退出登录
            </Button>
          )}
        </div>
      </header>
      <div className="flex justify-center bg-white px-4 sm:px-8 relative mt-12">
        <Card className="w-full max-w-none p-2 bg-gray-200 shadow-md rounded-lg h-100%">
          <div className="w-full bg-gray-200 flex flex-col items-center py-10">
            <AiOutlineUser className="w-24 h-24 rounded-full bg-white mb-4" />
            <div className="text-black text-2xl font-bold mb-1">{userName}</div>
            <div className="text-gray-400 mb-6">(个人主页)</div>
            <div className="flex gap-24 mb-6">
              <Button
                onClick={() => setIsCreateDialogOpen(true)}
                className="px-8 py-2 text-lg border border-white bg-black text-white hover:bg-gray-700 hover:text-white rounded-full"
                variant="outline"
              >
                创建课题组
              </Button>
              {/* 修改: 为加入课题组按钮添加点击事件 */}
              <Button
                onClick={() => setIsJoinDialogOpen(true)}
                className="px-8 py-2 text-lg border border-white bg-white text-black hover:bg-gray-100 rounded-full"
                variant="outline"
              >
                加入课题组
              </Button>
            </div>
          </div>
        </Card>
      
      {/* 创建课题组对话框 */}
      <Dialog open={isCreateDialogOpen} onOpenChange={setIsCreateDialogOpen}>
        <DialogContent className="sm:max-w-md">
          <DialogHeader>
            <DialogTitle className="text-xl font-semibold">创建新课题组</DialogTitle>
          </DialogHeader>
          <div className="grid gap-4 py-4">
            <div className="grid grid-cols-4 items-center gap-4">
              <label htmlFor="name" className="text-right font-medium text-gray-700">
                课题组名称
              </label>
              <Input
                id="name"
                name="name"
                value={createFormData.name}
                onChange={handleInputChange}
                className="col-span-3"
                placeholder="请输入课题组名称"
              />
            </div>
            <div className="grid grid-cols-4 items-center gap-4">
              <label htmlFor="institution" className="text-right font-medium text-gray-700">
                所属机构
              </label>
              <Input
                id="institution"
                name="institution"
                value={createFormData.institution}
                onChange={handleInputChange}
                className="col-span-3"
                placeholder="请输入所属机构"
              />
            </div>
            <div className="grid grid-cols-4 items-start gap-4">
              <label htmlFor="description" className="text-right font-medium text-gray-700 pt-2">
                课题描述
              </label>
              <Textarea
                id="description"
                name="description"
                value={createFormData.description}
                onChange={handleInputChange}
                className="col-span-3"
                placeholder="请输入课题描述"
                rows={3}
              />
            </div>
            <div className="grid grid-cols-4 items-start gap-4">
              <label htmlFor="research_area" className="text-right font-medium text-gray-700 pt-2">
                研究方向
              </label>
              <Textarea
                id="research_area"
                name="research_area"
                value={createFormData.research_area}
                onChange={handleInputChange}
                className="col-span-3"
                placeholder="请输入研究方向"
                rows={3}
              />
            </div>
          </div>
          <DialogFooter className="sm:justify-end">
            <Button
              type="button"
              variant="outline"
              onClick={() => setIsCreateDialogOpen(false)}
              className="!rounded-button whitespace-nowrap cursor-pointer"
            >
              返回
            </Button>
            <Button
              type="button"
              onClick={handleCreateSubmit}
              className="!rounded-button whitespace-nowrap cursor-pointer bg-black hover:bg-gray-800 text-white"
            >
              完成创建
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
      
      {/* 新增: 加入课题组对话框 */}
      <JoinGroupDialog 
        open={isJoinDialogOpen}
        onOpenChange={setIsJoinDialogOpen}
      />
      </div>
    </ProtectedRoute>
  );
}